import { useEffect, useState, useContext } from "react";
import { AppContext } from "../../api/store/context";
import Footer from "../footer";
import AlternatingCircles from "../loaders/good-animations";
import { Title } from "../titles/title";

export default function TermsAndConditions(props) {
  const [termsStatement, setTermsStatement] = useState([]);

  const { terms } = useContext(AppContext);

  useEffect(() => {}, []);

  return (
    <div style={{ marginTop: "4rem" }}>
      <Title title="Terms and Conditions" />
      <section style={{ height: "100vh" }} className="p-5">
        {terms.length ? (
          <p className="lead">{terms[0].term}</p>
        ) : (
          <div
            style={{ width: "100%", height: "50%" }}
            className="d-flex justify-content-center align-items-center"
          >
            <AlternatingCircles />
          </div>
        )}
      </section>
      <Footer />
    </div>
  );
}
